import { Component } from '@angular/core';

import { PanelComponent } from '@igo2/common/panel';
import {
  DataSourceService,
  IgoMap,
  ImageLayer,
  ImageLayerOptions,
  LAYER_DIRECTIVES,
  LayerOptions,
  LayerService,
  MAP_DIRECTIVES,
  MapViewOptions,
  MetadataLayerOptions,
  OSMDataSource,
  OSMDataSourceOptions,
  WMSDataSource,
  WMSDataSourceOptions
} from '@igo2/geo';

import { DocViewerComponent } from '../../components/doc-viewer/doc-viewer.component';
import { ExampleViewerComponent } from '../../components/example/example-viewer/example-viewer.component';

@Component({
  selector: 'app-legend',
  templateUrl: './legend.component.html',
  styleUrls: ['./legend.component.scss'],
  standalone: true,
  imports: [
    DocViewerComponent,
    ExampleViewerComponent,
    MAP_DIRECTIVES,
    PanelComponent,
    LAYER_DIRECTIVES
  ]
})
export class AppLegendComponent {
  public map: IgoMap = new IgoMap({
    controls: {
      attribution: {
        collapsed: true
      }
    }
  });

  public view: MapViewOptions = {
    center: [-73, 47.2],
    zoom: 7
  };

  constructor(
    private dataSourceService: DataSourceService,
    private layerService: LayerService
  ) {
    this.dataSourceService
      .createAsyncDataSource({
        type: 'osm'
      } satisfies OSMDataSourceOptions)
      .subscribe((dataSource: OSMDataSource) => {
        this.map.addLayer(
          this.layerService.createLayer({
            title: 'OSM',
            visible: true,
            baseLayer: true,
            source: dataSource
          } satisfies LayerOptions)
        );
      });

    this.layerService
      .createAsyncLayer({
        title: 'Legend with display: false',
        legendOptions: {
          display: false
        },
        sourceOptions: {
          type: 'wms',
          url: 'https://geoegl.msp.gouv.qc.ca/apis/ws/mffpecofor.fcgi',
          params: {
            LAYERS: 'sh_dis_eco',
            VERSION: '1.3.0'
          }
        }
      } satisfies ImageLayerOptions)
      .subscribe((layer: ImageLayer) => this.map.addLayer(layer));

    this.layerService
      .createAsyncLayer({
        title: 'Legend with html param',
        legendOptions: {
          html: '<h2 style="background-color: blue;">HTML legend</h2>'
        },
        sourceOptions: {
          type: 'wms',
          url: 'https://geoegl.msp.gouv.qc.ca/apis/ws/mffpecofor.fcgi',
          params: {
            LAYERS: 'sh_sreg_eco',
            VERSION: '1.3.0'
          }
        }
      } satisfies ImageLayerOptions)
      .subscribe((layer: ImageLayer) => this.map.addLayer(layer));

    this.layerService
      .createAsyncLayer({
        title: 'Legend with url param',
        legendOptions: {
          url: `data:image/jpeg;base64,`
        },
        sourceOptions: {
          type: 'wms',
          url: 'https://geoegl.msp.gouv.qc.ca/apis/ws/mffpecofor.fcgi',
          params: {
            LAYERS: 'sh_reg_eco',
            VERSION: '1.3.0'
          }
        }
      } satisfies ImageLayerOptions)
      .subscribe((layer: ImageLayer) => this.map.addLayer(layer));

    this.layerService
      .createAsyncLayer({
        title: 'Commissions scolaires anglophones',
        sourceOptions: {
          type: 'wms',
          url: 'https://geoegl.msp.gouv.qc.ca/apis/wss/all.fcgi',
          params: {
            LAYERS: 'WMS_MEQ_CS_ANGLO',
            VERSION: '1.3.0'
          }
        }
      } satisfies ImageLayerOptions)
      .subscribe((layer: ImageLayer) => this.map.addLayer(layer));

    this.layerService
      .createAsyncLayer({
        title: 'Rseau routier',
        sourceOptions: {
          type: 'wms',
          url: 'https://ws.mapserver.transports.gouv.qc.ca/swtq',
          params: {
            LAYERS: 'bgr_v_sous_route_res_sup_act',
            VERSION: '1.3.0'
          }
        }
      } satisfies ImageLayerOptions)
      .subscribe((layer: ImageLayer) => this.map.addLayer(layer));

    this.layerService
      .createAsyncLayer({
        title: 'Lieux habits',
        sourceOptions: {
          type: 'wms',
          url: 'https://ws.mapserver.transports.gouv.qc.ca/swtq',
          params: {
            LAYERS: 'lieuhabite',
            VERSION: '1.3.0'
          }
        }
      } satisfies ImageLayerOptions)
      .subscribe((layer: ImageLayer) => this.map.addLayer(layer));

    this.layerService
      .createAsyncLayer({
        title: 'Avertissements routiers',
        sourceOptions: {
          type: 'wms',
          url: 'https://ws.mapserver.transports.gouv.qc.ca/swtq',
          params: {
            LAYERS: 'evenements',
            VERSION: '1.3.0'
          }
        }
      } satisfies ImageLayerOptions)
      .subscribe((layer: ImageLayer) => this.map.addLayer(layer));

    const datasource: WMSDataSourceOptions = {
      type: 'wms',
      url: 'https://geoegl.msp.gouv.qc.ca/apis/wss/complet.fcgi',
      refreshIntervalSec: 15,
      params: {
        LAYERS: 'vg_observation_v_inondation_embacle_wmst',
        VERSION: '1.3.0'
      }
    };

    interface LayerOptionsWithMetadata
      extends LayerOptions,
        MetadataLayerOptions {}

    this.dataSourceService
      .createAsyncDataSource(datasource)
      .subscribe((dataSource: WMSDataSource) => {
        const layerOptions: LayerOptionsWithMetadata = {
          title: 'Embcles',
          source: dataSource,
          metadata: {
            url: 'https://www.donneesquebec.ca/recherche/fr/dataset/historique-publique-d-embacles-repertories-au-msp',
            extern: true
          }
        };
        this.map.addLayer(this.layerService.createLayer(layerOptions));
      });
  }
}
